/* ## Typographical Elements
--------------------------------------------- */
@import "body";

a,
button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.gallery img {
  transition: all .75s cubic-bezier(0, 1, .5, 1);
}

a {
  color: $link--color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link--hover-color;
    text-decoration: none;
  }
}

p {
  margin: 0 0 28px;
  padding: 0;
}

hr {
  border: 0;
  border-collapse: collapse;
  border-top: 1px solid #eee;
  clear: both;
  margin: 1em 0;
}

b,
strong {
  font-weight: 700;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}


