
.archive-description {
  background-color: $archive-description--background-color;
  @include rem( font-size, $archive-description--font-size );
  margin-bottom: 40px;
  padding: 60px;

  p:last-child {
    margin-bottom: 0;
  }

  @include media-query( 'sm' ) {
    padding: 30px;
    margin-bottom: 5%;
  }
}


