/* # Sexy Animations
---------------------------------------------------------------------------------------------------- */
@import url("https://fonts.googleapis.com/css?family=Oxygen|Rajdhani");
.wp-animation {
	opacity: 0;
}

.wp-animation.animated {
	opacity: 1;
}

@keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1 !important;
	}
}

