}

img {
	border-style: none;
}

svg:not(:root) {
	overflow: hidden;
}

code, kbd, pre, samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

button, input, optgroup, select, textarea {
	font: inherit;
	margin: 0;
}

optgroup {
	font-weight: bold;
}

button, input {
	overflow: visible;
}

button, select {
	text-transform: none;
}

button, html [type="button"], [type="reset"], [type="submit"] {
