	margin: 0;
	color: #000;
}

.sexy_popup_container .popupCloseButton {
	color: #333;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	position: absolute;
	top: -23px;
	right: -6px;
	font-size: 40px;
}

.sexy_popup_container .popupCloseButton i:hover {
	color: #777;
}

#popup_text {
	font-family: "Oxygen", sans-serif;
}

#popup_link {
	font-family: "Oxygen", sans-serif;
}

.popup_toast {
	position: absolute;
	text-align: center;
	display: none;
	box-shadow: 0px 3px 13px #555;
	bottom: 0;
	left: 0;
	right: 0;
	background: white;
	width: 200px;
	margin: 0 auto;
	color: #3c3c3c;
	margin-bottom: 2em;
	border-radius: 8px;
	z-index: 101;
}

.popup_toast p {
	margin: 0 !important;
	font-family: "Oxygen", sans-serif;
}

