.admin-bar #slider,
.admin-bar #close_slider{
  margin-top: 30px !important;
}

#slider {
  display: none;
  position: fixed;
  top: 0;
  right: -40%;
  background: rgba(34,32,32,.98);
  padding: 15px;
  overflow-y: scroll;
  overflow-x: hidden;
  height:100vh;
  z-index: 9999;

  .woocommerce .col2-set .col-1,
  .woocommerce-page .col2-set .col-1 {
    width: 100%;
    float: none;
  }

  section {
    width: 100%;
    display: none;
  }

  section:first-of-type {
    margin-top: 0px;
    position: fixed;
    display: block !important;
    background: rgba(34,32,32,1);
    z-index: 1;
    box-shadow: 0px 0px 12px rgba(0,0,0,.3);
    top: 0;
    height: 50px;
    margin-left: -15px;
    padding: 10px 20px;
  }

  .gform_wrapper ul.gfield_checkbox li label,
  .gform_wrapper ul.gfield_radio li label {
    text-align: left;
    padding-left: 5px;
  }
}


